import { ValidatorFn, AbstractControl, Validators } from '@angular/forms';
import { AbstractXelerateElement } from '../xelerate-elements/abstract-xelerate-element.component';

export function xelerateElmValidator(elm: AbstractXelerateElement, additionalValidators: ValidatorFn[]): ValidatorFn[] {
	let validators : ValidatorFn[] = [];
	if(elm.info.wrapclass.indexOf("required") > -1){
		validators.push(Validators.required);
	}
	validators.push((control: AbstractControl): {[key: string]: boolean} | null => {
		if(control.value == "R"){
			return {'customErr': true};
		}
		return null;
	})
	validators = validators.concat(additionalValidators);
	return validators;
	/* return (control: AbstractControl): {[key: string]: boolean} | null => {
		if(Validators.maxLength(2)){
			return {'customErr': true};
		}
		return null;
	} */
}