import { Component } from "@angular/core";
import { XelerateService } from "src/shared/services/xelerate.service";
import config from "../assets/config.json";
import labels from "../assets/LabelCodes.json";
import messages from "../assets/Messages.json";

@Component({
	selector: "app-root",
	templateUrl: "./app.component.html",
	styleUrls: ["./app.component.css"],
})
export class AppComponent {
	title = "Xelerate App";
	constructor(private xelerate: XelerateService) {
		xelerate.initAppConfig(config);
		xelerate.initLabelsMap(labels);
		xelerate.initMessagesMap(messages);
	}

	ngAfterViewInit() {
		this.xelerate.loadComponent({ compName: this.xelerate.appConfig.firstPage });
	}
}
