///// Directive to access non fielter filed native objects
import { Directive, ElementRef, Renderer2, Input, AfterViewInit } from '@angular/core';

@Directive({
	selector: '[filterFields="N"]'
})
export class NonFilterFields implements AfterViewInit {
	@Input() fc;
	@Input() info;
	ctrl

	constructor(public elRef: ElementRef, private renderer: Renderer2) {
	}

	ngAfterViewInit() {
		this.ctrl = this.elRef.nativeElement.querySelector("#" + this.info.id);
	}

	enable() {
		if (this.fc) {
			this.fc.enable();
		} else {
			this.renderer.removeAttribute(this.ctrl, 'disabled');
		}
	}

	disable() {
		if (this.fc) {
			this.fc.enable();
		} else {
			this.renderer.setAttribute(this.ctrl, 'disabled', 'true');
		}
	}
}