import { Component, OnInit, forwardRef, ElementRef } from "@angular/core";
import { ContextProvider } from '../../services/context-provider.service';
import { AbstractXelerateElement } from '../abstract-xelerate-element.component';
import { XelerateService } from 'src/shared/services/xelerate.service';
import { Subject } from 'rxjs';

@Component({
	selector: "xelerate-tags",
	templateUrl: "./xelerate-tags.component.html",
	providers: [
		{provide: AbstractXelerateElement, useExisting: forwardRef(()=>XelerateTagsComponent)}
	]
})
export class XelerateTagsComponent extends AbstractXelerateElement {
	tagListUpdates = new Subject<any>();
	constructor(cp: ContextProvider, xelerate: XelerateService, elmRef: ElementRef) {
		super(cp, xelerate, elmRef);
	}

	getOptionUpdatesObservable(){
		return this.tagListUpdates.asObservable();
	}

	updateTagList(tags) {
		//// Creating new options array just in case to ease the change detection for any custom code
		this.info.tags = [...tags];
		this.tagListUpdates.next(tags);
		/* this.info.options.length = 0;
		this.info.options.push(...options); */
	}

	ngOnDestroy(){
		this.tagListUpdates.complete();
		super.ngOnDestroy();
	}
}
