import { Pipe } from "@angular/core";
import { SlicePipe } from '@angular/common';

@Pipe({
	name: 'cardslimit'
})
export class CardsLimitPipe extends SlicePipe{

	cardsLimit=30;

    transform(value, start=0, end=this.cardsLimit) {
        if (value == null)
            return value;
        if (!this.supported(value)) {
            throw invalidPipeArgumentError(SlicePipe, value);
        }
        return value.slice(start, end);
	}
	
	private supported(obj) { return typeof obj === 'string' || Array.isArray(obj); }
}
function invalidPipeArgumentError(type, value) {
    return Error(`InvalidPipeArgument: '${value}' for pipe '${type}'`);
}