import { Directive, OnInit, OnDestroy, Input, ElementRef, Renderer2 } from "@angular/core";
import { XelerateService } from '../services/xelerate.service';
import { Subscription } from 'rxjs';

declare var jQuery: any;

@Directive({
	selector: '[xpopover]'
})
export class XeleratePopoverDirective implements OnDestroy {
	@Input()
	xpopover: any;
	selectorClass = 'popover-pnl';
	ctrlObj: any;
	pickrInstance: any;
	defaultParams: any;

	stateChanges: Subscription;
	valueChanges: Subscription;

	constructor(private popover: ElementRef,
		private xelerate: XelerateService,
		private renderer: Renderer2) {
	}

	ngAfterViewInit() {
		this.ctrlObj = this.popover.nativeElement;
		if (jQuery(this.ctrlObj).hasClass(this.selectorClass)) {
			let jQElm = jQuery(this.ctrlObj);
			let triggerInfo = this.xpopover;
			let triggerBehaviour = triggerInfo.triggerBehaviour == "CLICK" ? "click" : "hover";
			this.defaultParams = {
				content: this.ctrlObj,
				hideOnClick: 'toggle',
				trigger: triggerBehaviour,
				interactive: true,
				theme:'light-border',
				maxWidth: '100vw',
				animate: 'shift-toward',
				onCreate: (instance) => {
					jQuery(instance.popper).find(".XSELECT").each((ind,elm) => {
						let parentElm = jQuery(elm).parents(".popover-pnl")[0];
						jQuery(elm).select2({"dropdownParent":parentElm});
					})
				}
			  };
			this.initPopover();
		}
	}


	initPopover(initParams?) {
		let finalParams = {
			...this.defaultParams,
			...initParams
		}
		if (jQuery(this.ctrlObj).hasClass(this.selectorClass)) {			
			this.xelerate.tippy(document.getElementById(this.xpopover.triggerId),finalParams );
		}
	}

	ngOnDestroy() {
	}
}