import {
	Component,
	OnInit,
	OnDestroy,
	ChangeDetectorRef,
	ViewChildren,
	QueryList,
	ChangeDetectionStrategy,
	ElementRef,
	Input,
} from "@angular/core";
import { FormGroup } from "@angular/forms";
import { XelerateService } from "../../shared/services/xelerate.service";
import { ContextProvider } from "../../shared/services/context-provider.service";
import { XelerateDatatableDirective } from "src/shared/directives/datatable.directive";
import { UtilityServices } from "src/shared/services/utility.service";
import { Maintenance } from 'src/shared/services/maintenance.service';
export class SimpleBaseClass implements OnInit, OnDestroy {
	/* Mandatory properties starts */ @Input() heirarchy: any;
	@Input() compRef: any;
	@Input() transData: any;
	ifaceData: FormGroup;
	metaInfo;
	name: string = "Default";
	prefs: any;
	@ViewChildren(XelerateDatatableDirective) dataTableDirs: QueryList<XelerateDatatableDirective>;
	public maintenance: Maintenance
	/* Mandatory properties ends */ constructor(
		public xelerate: XelerateService,
		infoJSON: any
	) {
		this.metaInfo = UtilityServices.copyJSON(infoJSON);
	}
	ngOnInit() {
		this.xelerate.initScreen(this);
	}
	query() {
		this.maintenance.query(this);
	}
	delete(){
		this.maintenance.delete(this);
	}
	create(){
		this.maintenance.create(this);
	}
	modify(){
		this.maintenance.modify(this);
	}
	clear(){
		this.xelerate.data.loadScrData(this,{});
	}
	ngOnDestroy() {
		this.xelerate.destroyScreen(this);
	}
}
