import { Component, forwardRef, ElementRef } from "@angular/core";
import { ContextProvider } from '../../services/context-provider.service';
import { AbstractXelerateElement } from '../abstract-xelerate-element.component';
import { XelerateService } from 'src/shared/services/xelerate.service';
import { Subject } from 'rxjs';

@Component({
	selector: "xelerate-select",
	templateUrl: "./xelerate-select.component.html",
	providers: [
		{provide: AbstractXelerateElement, useExisting: forwardRef(()=>XelerateSelectComponent)}
	]
})
export class XelerateSelectComponent extends AbstractXelerateElement {
	optionUpdates = new Subject<any>();
	constructor(cp: ContextProvider, xelerate: XelerateService, elmRef: ElementRef) {
		super(cp, xelerate, elmRef);
	}

	getOptionUpdatesObservable(){
		return this.optionUpdates.asObservable();
	}

	updateOptions(options) {
		//// Creating new options array just in case to ease the change detection for any custom code
		this.info.options = [...options];
		this.optionUpdates.next(options);
		/* this.info.options.length = 0;
		this.info.options.push(...options); */
	}

	ngOnDestroy(){
		this.optionUpdates.complete();
		super.ngOnDestroy();
	}
}
