import { Injectable } from '@angular/core';
import { UtilityServices } from 'src/shared/services/utility.service';

@Injectable({
	providedIn: 'root'
})
export  class ComponentsProvider {
	
	async getComponent(param){
		let component = null;
		//// Look for internal components vailable
		let module = await this.importComponent(param);
		if(module){
			component = module[param.compName];
		} else if(param.customImport){
			//// If no internal components found, get component from user implementation
			component = await UtilityServices.invokeMethod(param, "customImport");
		}
		return component;
	}

	importComponent(param){
		//// Should be listed with all screens developed, TBD - Have a export option from Designer or manual update?
		switch(param.compName){
			 case 'CustomerQuery_xelerate': return import('./customerquery_xelerate/customerquery_xelerate.component');
 case 'Login': return import('./login/login.component');

		}
		return null;
	}
}
