	makeHeaderCollapse(){
		jQuery(window).scroll(function () {
			jQuery('html,body').animate(10000);
			let scroll = jQuery(window).scrollTop();
			if (scroll >= 5) {
				jQuery("body").addClass("head-up");	
			} else {
				jQuery("body").removeClass("head-up");	
			}
		});  
	}

