import { Directive, Inject, ViewContainerRef, Input, OnInit, OnDestroy, ElementRef } from '@angular/core';
import { XelerateService } from '../services/xelerate.service';

@Directive({
	selector: '[launcher]',
})
export class LauncherDirective implements OnInit, OnDestroy {
	@Input() launcher: string;
	constructor(private vcr: ViewContainerRef, public xelerate: XelerateService) { }

	ngOnInit() {
		this.xelerate.launcherRefs.push(this);
	}

	ngOnDestroy() {
		let index = this.xelerate.launcherRefs.indexOf(this);
		if (index > -1) {
			this.xelerate.launcherRefs.splice(index, 1);
		}
	}

}